.main-canvas{
  position: relative;
  width: calc(100% - 160px);
  overflow: hidden;
  .canvas-steper{
    display: flex;
    align-items: center;
    position: absolute;
    bottom: 16px;
    left: 16px;
    height: 26px;
    border-radius: 13px;
    background: #363945;
    z-index: 1;
    overflow: hidden;
    .steper-minus, .steper-plus{
      padding: 6px;
      cursor: pointer;
      svg{
        display: block;
      }
      &:hover{
        background: #222531;
      }
    }
    .steper-percentage{
      padding: 6px;
    }
  }
  .canvas-container{
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    padding: 60px;
    background: #111421;
    transition: none;
  }
}
